












import Vue from 'vue';
import Resizer from './Resizer.vue';
import store from '@/store';

export default Vue.extend({
  name: 'Editor',
  computed: {
    w: {
      get() {
        return store.state.resizer.pos;
      },
      set(value: string) {
        store.commit('UPDATE_POS', value);
      },
    },
    content: {
      get() {
        return store.state.rawJson;
      },
      set(value: string) {
        store.commit('UPDATE', value);
      },
    },
  },
  components: {
    Resizer,
  },
});
