
import * as json2Ast from 'json-to-ast';

export interface Node {
    type: string;
}

export interface JString extends Node { value: string; }
export interface JNum extends Node { value: number; }
export interface JBool extends Node { value: boolean; }
export interface JNull extends Node { value: null; }

export interface JPair  extends Node {
    key: string;
    value: AST;
}

export interface JArray extends Node {
    children: [AST];
}

export interface JObj extends Node {
    children: [JPair];
}

export type AST = JString | JNum | JBool | JNull | JArray | JObj;

export type Result = object | AST;

export function parse(jsonString: string): Result {
    try {
        return json2Ast.default(jsonString) as AST;
    } catch (e) {
        return {
            error: e,
        };
    }
}
