








import Vue from 'vue';
import store from '@/store';
import * as json2ast from '../ast';
import Tree from './Tree.vue';

export default Vue.extend({
  name: 'Viewer',
  computed: {
    selectState: {
      get() {
        if (store.state.resizer.inProgress) {
          return 'none';
        } else {
          return 'auto';
        }
      },
    },
    ast() {
      return json2ast.parse(store.state.rawJson);
    },
  },
  components: {
    Tree,
  },
});
